@import "../../assets/scss/app.scss";

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  box-sizing: border-box;

  display: flex;
  align-items: center;
  justify-content: space-between;

  padding-top: 18px;
  padding-right: 16px;
  margin-left: -7px;

  @include desktop() {
    padding-top: 18px;
    padding-right: 58px;
    padding-left: 28px;
    margin-left: 0;
  }

  .logo {
    display: block;
    text-decoration: none;

    img {
      display: block;
      width: 146px;
      height: 56px;

      @include desktop() {
        width: 312px;
        height: 131px;
      }

    }

  }

  .links {
    @include onlyDesktop() {
      display: flex;
    }

    .link {
      text-decoration: none;
      font-style: normal;
      font-weight: 500;
      font-size: 15px;
      line-height: 150%;
      text-shadow: 0 0 12px rgba(0, 0, 0, 0.64);

      &:not(:first-child) {
        margin-left: 48px;
      }
    }

  }


  .iconButtons {
    @include onlyDesktop(flex) {
      align-items: center;
    }

    .icon {
      width: 40px;
      height: 40px;
      margin-left: 32px;
    }

    .connectBtn {
      margin-left: 28px;
      width: 142px;
      height: 40px;
    }

    .connect {
      margin-left: 28px;

      width: 142px;
      height: 40px;
      position: relative;
      @include center();

      img {
        display: block;
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }

      .connectHover {
        display: none;
      }

      &:hover .connectHover {
        display: block;
      }

      p {
        position: relative;
        z-index: 1;
        @include PressStart();
        font-size: 15px;
        line-height: 160%;
        letter-spacing: -0.02em;
        color: #715021;

      }
    }

  }

  .burgerButton {
    @include onlyMobile() {
      z-index: 101;
      @include button();
    }


  }

}