@import "../../assets/scss/app.scss";

.home {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 183px 16px 25px;


  @include desktop() {
    padding: 0;
    min-height: 100vh;
    @include center();
  }

  .innerWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;

    .title {
      @include PressStart();
      font-size: 22px;
      line-height: 145%;
      text-align: center;
      width: 288px;

      @include desktop() {
        font-size: 38px;
        line-height: 150%;
        width: 700px;
      }
    }

    .enterButton {
      width: 116px;
      height: 48px;
      margin-top: 72px;

      @include desktop() {
        width: 192px;
        height: 80px;
        margin-top: 82px;
      }
    }

    .icons {
      @include onlyMobile(flex) {
        justify-content: center;
        margin-top: 72px;

        .icon {
          &:not(:first-child) {
            margin-left: 32px;
          }
        }

      }
    }
  }
}