.app {
  background: #130B25;
  position: relative;
  max-height: 100vh;
  overflow: scroll;

  &_fixed {
    overflow: hidden;
    height: 100vh;
  }

  .animWrapper {
    position: relative;

    .sun {
      z-index: 10;
      display: block;
      position: absolute;
      left: 86px;
      filter: brightness(5);
    }

    .back {
      z-index: 20;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: auto;
    }

    .air {
      z-index: 5;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: auto;
    }

    .rainWrapper {
      position: absolute;
      overflow: hidden;
      left: 0;
      z-index: 25;
      width: 100vw;
      height: 100vh;
    }

    .componentsWrapper {
      position: relative;
      z-index: 30;
    }

  }


}